.back {
  position: absolute;
  z-index: -999;
  margin: 30px;
  font-size: 200px;
  font-weight: 800;
  margin-top: 57px;
  color: black;
}
.back:hover {
  color: transparent;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.9);
}
.step__card {
  object-fit: contain;
  padding: 64px 32px 64px 32px;
  border-radius: 20px;
  /*background: linear-gradient(4deg, rgba(4,107,162,1) 0%, rgba(2,126,193,1) 37%, rgba(0,166,255,1) 88%);
	#095365
	color:rgba(2,126,193,1);
	*/
  background: linear-gradient(
    32deg,
    rgba(24, 218, 255, 0.9) 0%,
    rgba(91, 255, 205, 0.9) 100%
  );
  width: fit-content;
  color: #095365;
  height: fit-content;
  font-family: "google";
  max-width: 400px;
  box-shadow: 0 4px 8px rgb(0 0 0 / 13%);
  position: relative;
  z-index: 999;
  margin: 10px;
  cursor: context-menu;
}
span:hover > .back {
  filter: blur(4px);
  animation: shake 1s;
  animation-iteration-count: linear;
}

