.logo__block {
  background: white;
  padding: 15px;
  width: fit-content;
  max-width: 70px;
  border-radius: 20px;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgb(0 0 0 / 13%);
}
p {
  margin: 0px;
  font-size: 18px;
  /*letter-spacing:0.02em;*/
}
h2 {
  margin: 0px;
  font-weight: 800;
  margin-bottom: 10px;
}
.second__card {
  background: linear-gradient(
    4deg,
    rgba(4, 107, 162, 0.9) 0%,
    rgba(2, 126, 193, 0.9) 37%,
    rgba(0, 166, 255, 0.9) 88%
  );
  color: rgba(2, 126, 193, 1);
}
.second__card > p {
  color: white;
}
.second__card > h2 {
  color: white;
}
.third__card {
  background: linear-gradient(
    to bottom,
    rgba(255, 212, 64, 0.9) 0%,
    rgba(255, 88, 88, 0.9) 100%
  );
  color: #5d114c;
}
